<template>
  <div class='two-pager'>
    <section id='portait'>
      <img class="rounded" src="https://picsum.photos/1200/1080">
    </section>
    <section id='content' class="rounded">
      <div class="media m-2">
        <img class="mr-3 rounded"
             src="https://picsum.photos/64/64"
             alt="Generic placeholder image">
        <div class="media-body">
          <h5 class="mt-0">Media heading</h5>
          Cras sit amet nibh libero, in gravida nulla. Nulla vel metus scelerisque ante sollicitudin. Cras purus odio, vestibulum in vulputate at, tempus viverra turpis. Fusce condimentum nunc ac nisi vulputate fringilla. Donec lacinia congue felis in faucibus.
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    msg: String
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.two-pager {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 10px auto 10px;
  grid-template-rows: 10px auto;
  grid-template-areas:
    ".    .    ."
    ". portait ."
    ". content ."
    ".    .    .";
}
#portait {
  min-width: 300px;
  min-height: 567px;
  height: 100%;
  background-color: lightcoral;
  grid-area: portait;
}

#content {
  min-width: 300px;
  min-height: 568px;
  background-color: lightskyblue;
  grid-area: content;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.media img {
  width: 64px;
  height: 64px;
}
@media screen and (min-width: 576px) {
  .two-pager {
    grid-template-rows: 10px 40% 55% 10px;
  }
}

@media screen and (min-width: 992px) {
  .two-pager {
    justify-self: right;
    grid-template-columns: 10% auto auto 10%;
    grid-template-rows: 10% auto 10%;
    grid-template-areas:
      ".    .     .      ."
      ". portait content ."
      ".    .     .      .";
  }
}

/* max-width: 560px; */
/* max-height: 500px; */
/* justify-self: end; */
</style>



